import { Component, OnInit, EventEmitter, Output } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { ReseniasService } from '../../services/resenias.service';
import { CDTService } from 'src/app/services/cdt.service';
import { DataService } from 'src/app/services/data.service';
import { BankService } from 'src/app/services/bank.service';
import { AuthService } from '../../services/auth.service';
import { interval } from 'rxjs';


@Component({
  selector: 'app-detail-cdt',
  templateUrl: './detail-cdt.component.html',
  styleUrls: ['./detail-cdt.component.css']
})
export class DetailCDTComponent implements OnInit {

  filtersLoaded: Promise<boolean>;
  idProd: number;
  private sub: any;
  proceso: any;
  resenias: any[];
  cdt: any;
  crear: boolean;
  noCargado = false;

  tieneMontoMinimo = false;

  tieneResenias = false;

  comentario: string;
  puntaje: number;



  constructor(private authservice: AuthService, private reseniasService: ReseniasService, private cdtService: CDTService, private router: Router, private route: ActivatedRoute, private dataService: DataService, private bankService: BankService) { }


  ngOnInit() {
    //this.getCDT();
    this.crear = false;
    this.cdt = this.dataService.obtenerCDT();
    this.cdt.producto_bancario.banco.calificacion_promedio = Math.round(this.cdt.producto_bancario.banco.calificacion_promedio);
    if (this.cdt == undefined || this.cdt === null) {
      this.router.navigate(['listado-cdt']);
    } else 
    {
      const secondsCounter = interval(100);
      this.getReviewsBank();
      secondsCounter.subscribe(n => this.getReviewsBank());

    }
  }

  getReviewsBank(){
    this.bankService.getReviewsBank(this.cdt.producto_bancario.banco.slug)
    .subscribe(res => {
      if (res.count == 0) {
        this.tieneResenias = false;
      }
      if(res.results != this.resenias){
        this.resenias = res.results;
      }
      this.noCargado = false;
    },
    err =>{
      console.log(err);
    });
  }


  /**
  * The output which tells the parent component
  * that the user no longer wants to create a provider
  */
  @Output() cancel = new EventEmitter();
  /**
  * The output which tells the parent component
  * that the user created a provider
  */
  @Output() create = new EventEmitter();


  cargarReseniasProducto(idProd) {
    this.reseniasService.cargarResenias(idProd).subscribe(res => {
      this.reseniasService.actualizarResenias(res);
    },
      err => {
        console.log(err);
        return false;
      });
  }
  createResena() {
      var resenia = {
      comentario : this.comentario,
      puntaje : this.puntaje
    }
    console.log(resenia);
    this.bankService.createReviewBank(this.cdt.producto_bancario.banco.slug, resenia)
      .subscribe(respons => {
        console.log(respons);
        this.create.emit();
      },
      error => {
        console.log(error.error)
      })
  }


  showHideCreate() {
    this.crear = !this.crear;
  }

  cancelCreation(): void {
    this.cancel.emit();
  }


}
