import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';

@Injectable()
export class DataService {

cdt : any;
slug : string;
showText : boolean;

guardarCDT(cdt){
    this.cdt = cdt;
}
obtenerCDT(){
    return this.cdt;
}


guardarSlug(slug){
    this.slug = slug;
}
obtenerSlug(): any{
    return this.slug;
}

guardarShowTextTrue()
{
    this.showText = true;
}

guardarShowTextFalse()
{
    this.showText = false;
}
obtenerShowText()
{
    return this.showText;
}

}