import { Component, OnInit, HostListener, ElementRef, ViewChild } from '@angular/core';
import { AuthService } from '../../services/auth.service';
import { interval } from 'rxjs';
@Component({
  selector: 'app-inicio',
  templateUrl: './inicio.component.html',
  styleUrls: ['./inicio.component.css']
})
export class InicioComponent implements OnInit {
  innerHeight: number;
  innerWidth: number;
  peque = false;
  footerhi: number;
  richie_altura: number;
  richie_ancho: number;
  logeado: boolean;
  ancho_frase: number;
  width_eslogan: number;
  flipeo1 = "card1";
  @ViewChild('esloganid') elementView: ElementRef;
  espacio_flotante: number;

  constructor(private authservice: AuthService, private elementRef: ElementRef) { }


  @HostListener('window:resize', ['$event'])
  onResize(event) {
    this.authservice.setInicio(true);
    const secondsCounter2 = interval(100);
    secondsCounter2.subscribe(n => this.verificarlog());
    this.innerWidth = window.innerWidth;
    this.innerHeight = window.innerHeight;
    this.richie_altura = (this.innerHeight / 100) * 60;
    this.richie_ancho = (this.innerWidth / 100) * 60;
    this.footerhi = this.authservice.darheighfooter();
    this.ancho_frase = (this.innerWidth / 100) * 30;
    this.width_eslogan = (this.innerWidth / 100) * 70;
    this.espacio_flotante = (73.063*this.width_eslogan)/867.853 + 40 + 50; 
    if (this.innerWidth < 1067) {
      this.peque = true;
    } else {
      this.peque = false;
    }
  }

  flipar1() {
    if (this.flipeo1 == "card1") {
      this.flipeo1 = "card1 flipped";
    } else {
      this.flipeo1 = "card1";
    }

  }

  verificarlog = function () {

    this.logeado = this.authservice.esregistrado();
  }


  ngOnInit() {

    this.authservice.setInicio(true);
    const secondsCounter2 = interval(100);
    secondsCounter2.subscribe(n => this.verificarlog());
    this.innerWidth = window.innerWidth;
    this.innerHeight = window.innerHeight;
    this.richie_altura = (this.innerHeight / 100) * 60;
    this.richie_ancho = (this.innerWidth / 100) * 60;
    this.footerhi = this.authservice.darheighfooter();
    this.ancho_frase = (this.innerWidth / 100) * 30;
    this.width_eslogan = (this.innerWidth / 100) * 70;
    this.espacio_flotante = (73.063*this.width_eslogan)/867.853 + 40 + 50; 
    if (this.innerWidth < 1067) {
      this.peque = true;
    } else {
      this.peque = false;
    }
  }

  loggedIn = function(){
    return this.authservice.loggedIn();
  }


}
