    {path: 'ahorra', component: PreguntasAhorroComponent, canActivate:[AuthGuard]},
    {path: 'adquiere', component: ConstruccionComponent, canActivate:[AuthGuard]},
    {path: 'registro', component: RegistrograndeComponent},
    {path: 'dashboard', component: DashboardComponent, canActivate:[AuthGuard]},
    {path: 'preguntas-cdt', component: PreguntasCdtComponent, canActivate:[AuthGuard]},
    {path: 'listado-cdt', component: ListadoCdtComponent, canActivate:[AuthGuard]},
    {path: 'CDT', component: DetailCDTComponent, canActivate:[AuthGuard]},
    {path: 'resenias', component: ReseniasComponent, canActivate:[AuthGuard]},
    {path: 'Banco', component: DetailBankComponent, canActivate:[AuthGuard]},
    {path: 'lista-bancos', component: ListBankComponent, canActivate:[AuthGuard]}
