import { Component, OnInit } from '@angular/core';
import { ReseniasService } from '../../services/resenias.service';
import { AuthService } from '../../services/auth.service';

@Component({
  selector: 'app-resenias',
  templateUrl: './resenias.component.html',
  styleUrls: ['./resenias.component.css']
})
export class ReseniasComponent implements OnInit {

  resenias = [];


  constructor(private authservice: AuthService, private reseniasService : ReseniasService) { }




  cargarResenias = function(){

    this.reseniasService.darResenias().subscribe(res => {
      this.resenias = res;
    },
      err => {
        console.log(err);
        return false;
      });

  }

  ngOnInit() {
    this.cargarResenias();
    this.authservice.setInicio(false);
  }

}
