import { Component, Inject, OnInit } from '@angular/core';
import { MatDialog, MatDialogConfig } from '@angular/material';
import { EditarPerfilComponent } from '../editar-perfil/editar-perfil.component';
import { CreateReseniaComponent } from '../create-resenia/create-resenia.component';
import { AuthService } from '../../services/auth.service';
import { userService } from 'src/app/services/user.service';




@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.css']
})
export class DashboardComponent implements OnInit {
  user: any;

  numAle: number;

  constructor(public dialog: MatDialog, private authservice : AuthService) {
  }
  ngOnInit() {
    this.authservice.setInicio(false);
    this.authservice.darUsuario().subscribe(data => {
      this.user = JSON.parse(data);
      console.log(data);
    }, err => {
      console.log(err);
    });

    this.numeroAleatorio();

  }  


  openEdit() {
    console.log("estoy aqui")

    const dgc = new MatDialogConfig();
    dgc.autoFocus = true;
    //dgc.width = "60%";
    this.dialog.open(EditarPerfilComponent, dgc);
  }


  openEditR() {
    const dgc = new MatDialogConfig();
    dgc.autoFocus = true;
    dgc.width = "30%";
    this.dialog.open(CreateReseniaComponent, dgc);
  }

    numeroAleatorio() {
    this.numAle = Math.round(Math.random()*30);
    }



}
