import { Injectable } from '@angular/core';
import { Http, Headers } from '@angular/http';
import { HttpClient, HttpHeaders, HttpErrorResponse } from '@angular/common/http';
import { map } from 'rxjs/operators';
import { Observable, throwError } from 'rxjs';
import { catchError, retry } from 'rxjs/operators';
import { of } from 'rxjs';



const httpOptions = {
  headers: new HttpHeaders({
    'Content-Type': 'application/json'
  })
}

@Injectable()
export class AuthService {
  authToken: any;
  user: any;
  footerhight: number;
  regristrado: boolean;
  claseRegistro: boolean;
  esinicio: boolean;
  cont: boolean;

  constructor(private http: Http, private http2: HttpClient) {
    this.regristrado = false;
    this.claseRegistro = false;
    this.esinicio = false;
    this.cont = false;

  }


  handleError(error: HttpErrorResponse) {
    if (error.error instanceof ErrorEvent) {
      // A client-side or network error occurred. Handle it accordingly.
      console.error('An error occurred:', error.error.message);
    } else {
      // The backend returned an unsuccessful response code.
      // The response body may contain clues as to what went wrong,
      console.error(
        `Backend returned code ${error.status}, ` +
        `body was: ${error.error}`);
    }
    // return an observable with a user-facing error message
    return throwError(
      'Something bad happened; please try again later.');
  };

  registerUser(user) {
    let headers = new Headers();
    headers.append('Content-Type', 'application/json');
    try {
      return this.http.post('http://157.230.14.37:8080/api/datos-registro/', user, { headers: headers });
    } catch (ERROR) {
      console.log("asjdioaioqoeoqeioqeioaqio");
      return ERROR;
    }
  }

  registerUser2(user) {
    return this.http2.post<any>('http://157.230.14.37:8080/api/usuarios/', user, httpOptions);
  }

  existeCorreo(correo) {
    return this.http2.get<any>('http://157.230.14.37:8080/api/datos-registro/' + correo + "/existe/");
  }

  authenticateUser(user) {
    return this.http2.post<any>('http://157.230.14.37:8080/api/token-auth/', user, httpOptions);
  }

  getProfile(token, email) {
    const httpOptions = {
      headers: new HttpHeaders({
        'Content-Type': 'application/json',
        'Authorization': 'token ' + token
      })
    }
    return this.http2.get<any>('http://157.230.14.37:8080/api/usuarios/' + email, httpOptions);
  }

  storeUserData(token) {
    localStorage.setItem('id_token', token);
    this.authToken = token;
  }

  guardarUsuario(usuario) {
    var user = JSON.stringify(usuario);
    localStorage.setItem('user', user);
    this.user = user;
  }

  loadToken() {
    const token = localStorage.getItem('id_token');
    this.authToken = token;
  }


  loadUser() {
    const user = localStorage.getItem('user');
    this.user = user;
  }

  darUsuario() : Observable<any> {
    this.loadUser();
    return of(this.user);
  }

  loggedIn() {
    if (localStorage.id_token == undefined) {
      return false
    } else {
      return true
    }
  }

  logout() {
    this.authToken = null;
    this.user = null;
    localStorage.clear();

  }

  guardarheighfooter(hig) {
    this.footerhight = hig;
  }

  darheighfooter() {
    return this.footerhight;
  }

  estadoregistro(bo) {
    this.regristrado = bo
  }

  esregistrado() {
    return this.regristrado;
  }

  darclaseRegistro() {
    return this.claseRegistro;
  }
  setclaseRegistro(clase) {
    this.claseRegistro = clase;
  }

  darInicio() {
    return this.esinicio;
  }

  setInicio(inic) {
    this.esinicio = inic;
  }

  guardarsegundociclo(cont) {
    this.cont = cont;
  }

  sacarsegundociclo() {
    return this.cont;
  }

}