import { Component, OnInit, ElementRef, HostListener, ViewChild } from '@angular/core';
import { AuthService } from './services/auth.service';
import { interval } from 'rxjs';
import { DataService } from './services/data.service';
@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit {
  title = 'angular-src';
  inicio = "flip-card-inner";
  espaciado_cards: number;
  @ViewChild('footer') elementView: ElementRef;
  constructor(private authservice: AuthService, private dataService: DataService) { }

  verfiricarInic = function () {
    if (window.innerWidth <= 400) {
      this.inicio = "flip-card-inner-mobile";
    } else {
      if (this.authservice.darInicio()) {
        this.inicio = "flip-card-inner-no-inic";
      } else {
        this.inicio = "flip-card-inner";
      }
    }
  }

  mandarTextCatalogo() {
    this.dataService.guardarShowTextTrue();
  }
  mandarTextQuestion() {
    this.dataService.guardarShowTextFalse();
  }

  loggedIn = function(){
    return this.authservice.loggedIn();
  }


  @HostListener('window:resize', ['$event'])
  onResize(event) {
    this.espaciado_cards = (window.innerHeight/100)*2;
  }


  ngOnInit() {
    this.espaciado_cards = (window.innerHeight/100)*3;
    if (this.elementView != undefined && this.elementView != null) {
      this.authservice.guardarheighfooter(this.elementView.nativeElement.offsetHeight);
      console.log('no llega null en el  app')
    } else {
      console.log('tambien llega null en el app')
    }
    const secondsCounter = interval(100);
    secondsCounter.subscribe(n => this.verfiricarInic());

  }
}
