    constructor(private authservice: AuthService, private route: ActivatedRoute, private cdtService : CDTService, private dataService : DataService) {
    }
    /**
     * 
     */
    getCDTS(): void {
        this.cdtService.getCDTs()
            .subscribe(cdts => {
                this.get = cdts;
                this.cdts = this.get.results;
                for (let cdt of this.cdts) {
                    if(cdt.monto_minimo === null)
                    {
                        cdt.monto_minimo = "-";
                    }
                    else
                    {
                        cdt.monto_minimo = "$ "+ cdt.monto_minimo;
                    }
                }
 
            });
    }

    refreshCDTs(monto,plazo) : void {
        this.plazoBusqueda = plazo * 30;
        this.montoBusqueda = monto;
        this.showMoreData = true;
        this.cdtService.getCDTsFiltered(monto,this.plazoBusqueda)
        .subscribe(cdts => {
            this.cdts = cdts;
            for (let cdt of this.cdts) {
                cdt.plazo_meses = cdt.plazo_min_dias/30;
                cdt.plazo_meses = Math.round(cdt.plazo_meses);
                cdt.dinero = (this.montoBusqueda*Math.pow(Math.pow(((cdt.tasa/100)+1),(1/cdt.plazo_meses)),(cdt.plazo_meses-1)));
                cdt.dinero = Math.round(cdt.dinero/1000)*1000;
                cdt.dinero = this.printNo(cdt.dinero);
            }
        });
    }

    mandarCDT(cdt): void {
        this.dataService.guardarCDT(cdt);
    }


    ngOnInit() {
        this.showText = this.dataService.obtenerShowText()
        this.authservice.setInicio(false);
        this.getCDTS();
    }

     printNo(num) {
        return Number(num).toLocaleString('en-GB');
      }
