import { Component, OnInit } from '@angular/core';
import { AuthService } from '../../services/auth.service';
import { Router } from '@angular/router';
import { NgFlashMessageService } from 'ng-flash-messages';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {
  username: String;
  password: String;
  inputvalido = "";
  avisoLoginText = "";
  avisoLoginTextTipo = "text-info";
  inputPassvalido = "";
  avisoPassText = "";

  constructor(
    private authService: AuthService,
    private router: Router,
    private flashMessage: NgFlashMessageService) { }

  ngOnInit() {
  }

  onLoginSubmit() {
    const user = {
      username: this.username,
      password: this.password
    }

    console.log(user);
    this.authService.guardarsegundociclo(false);
    this.authService.authenticateUser(user).subscribe(data => {
      console.log(data);
      if (data.token != undefined && data.token != null) {
        this.authService.storeUserData(data.token);
        var completo = user.username.replace(/\./g, "_");
        completo = completo.replace(/@/g, "");
        this.flashMessage.showFlashMessage({
          messages: ['Acceso correcto'],
          type: 'success', timeout: 5000
        });
        
        this.authService.getProfile(data.token, completo).subscribe(data =>{
          console.log('guardado');
          console.log(data);
          this.authService.guardarUsuario(data);
        }, err =>{
          console.log('no guardado');
          console.log('error');
        });
      } else {
        var err = "Error de login. ";
        if(data.non_field_errors != undefined && data.non_field_errors != null ){
          err += data.non_field_errors;
        }
        err += " . Volver a intentar";
        this.avisoLoginTextTipo ='text-danger';
        this.avisoLoginText = err;
        this.flashMessage.showFlashMessage({
          messages: [err],
          type: 'danger', timeout: 5000
        });
      }
    },
    error => {
      console.log(error);
      var err = "Error de login. ";
        if(error.non_field_errors != undefined && error.non_field_errors != null ){
          err += error.non_field_errors;
        }
        err += " . Volver a intentar";
        this.avisoLoginTextTipo ='text-danger';
        this.avisoLoginText = err;
        this.flashMessage.showFlashMessage({
          messages: [err.concat(error.error)],
          type: 'danger', timeout: 5000
        });
    });
  }

  llevaralregistro() {
    this.authService.estadoregistro(false);
  }

}