import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';

const API_URL = "http://157.230.14.37:8080/api/";
const cdt = 'CDTs';


@Injectable()
export class CDTService {
    constructor(private http: HttpClient) { }

    getCDTs() : Observable<any[]> {
        return this.http.get<any[]>(API_URL + cdt);
    }
    getCDT(cdt) : Observable<any> {
        return this.http.get<any>(API_URL + cdt + '/' + cdt);
    }

    getCDTsFiltered(monto, plazo) : Observable<any[]> {
        return this.http.get<any[]>(API_URL + cdt + '?monto='+monto+'&plazo='+plazo);

    }
}