import { Component, OnInit } from '@angular/core';
import { AuthService } from '../../services/auth.service';
import { Router } from '@angular/router';
import { interval } from 'rxjs';
import { NgFlashMessageService } from 'ng-flash-messages';
import { delay } from 'rxjs/operators';

@Component({
  selector: 'app-registro',
  templateUrl: './registro.component.html',
  styleUrls: ['./registro.component.css']
})
export class RegistroComponent implements OnInit {
  nombre: String;
  apellido: String;
  email: String;
  clave: String;
  re_pass: String;
  telefono: Number;
  ciudad: String;
  fecha: Date;

  clase = "container2";
  estilo = "";
  ahora = 1;
  email2 = "";
  acepta = false;
  correoValido = false;
  inputvalido = "";
  avisoCorreoText = "";
  avisoCorreoTextTipo = "text-info";
  inputPassvalido = "";
  avisoPassText = "";
  avisoPassTextTipo = "text-info";
  modaldatatoggle = "";
  modaldatatarget = "";

  registroerror = false;
  erroremail : String;
  errornombre : String;
  errorapellido : String;
  errorciudad : String;
  errorfecha : String;
  errortelefono : String;
  errorpassword : String;


  constructor(
    private authService: AuthService,
    private router: Router,
    private flashMessage: NgFlashMessageService) { }

  ngOnInit() {
    this.inputvalido = "";
    this.avisoCorreoText = "";
    this.avisoCorreoTextTipo = "text-info";
    this.modaldatatoggle = "";
    this.modaldatatarget = "";
    const secondsCounter = interval(100);

    secondsCounter.subscribe(n => this.onChangeContra());

    /* pruebas
    var rand = Math.random() * (9999999999 - 1111111111) + 1111111111;
    this.nombre = "prueba" + rand;
    this.apellido = "prueba" + rand;
    this.email = "prueba" + rand + "@gmail.com";
    this.telefono = rand;
    this.clave = "prueba" + rand;
    this.re_pass = "prueba" + rand;
    this.ciudad = "bogota";
    */
  }

  delay = function (ms: number) {
    return new Promise(resolve => setTimeout(resolve, ms));
  }

  pasarasiguiente = function () {
    if (this.ahora == 1) {
      if (this.email == this.email2) {
      } else {
        this.avisoCorreoText = "Validando correo...";
        this.avisoCorreoTextTipo = 'text-warning';
        if (this.email == undefined && this.email == null) {
          this.avisoCorreoText = 'Ingresa un correo';
          this.avisoCorreoTextTipo = 'text-danger';
          this.inputvalido = "is-invalid";
        } else {
          if (this.email.trim() == "") {
            this.avisoCorreoText = 'Ingresa un correo';
            this.avisoCorreoTextTipo = 'text-danger';
            this.inputvalido = "is-invalid";
          } else {
            var separado = this.email.split("@");
            if (separado.length != 2) {
              console.log('el correo no es válido');
              this.avisoCorreoText = 'el correo no es válido';
              this.avisoCorreoTextTipo = 'text-danger';
              this.inputvalido = "is-invalid";
            } else {
              var separado2 = separado[1].split(".");
              if (separado2.length < 2) {
                this.avisoCorreoText = 'el correo no es válido';
                this.avisoCorreoTextTipo = 'text-danger';
                this.inputvalido = "is-invalid";
              } else {
                var completo = this.email.replace(/\./g, "%1%");
                completo = completo.replace(/@/g, "%2%");
                console.log(this.email);
                console.log(completo);

                this.authService.existeCorreo(completo).subscribe(data => {
                  console.log(data);
                  if (data.existe == "SI") {
                    console.log("si existe");
                    this.avisoCorreoText = 'el correo ya está registrado';
                    this.avisoCorreoTextTipo = 'text-danger';
                    this.inputvalido = "is-invalid";
                  } else {
                    console.log("No existe");
                    this.avisoCorreoTextTipo = 'text-success';
                    this.avisoCorreoText = 'Puedes seguir :)';
                    this.inputvalido = "is-valid";

                    setTimeout(() => {
                      this.ahora = 2;
                      this.inputvalido = "";
                      this.avisoCorreoText = "";
                      this.avisoCorreoTextTipo = "text-info";
                      this.modaldatatoggle = "";
                      this.modaldatatarget = "";
                    },
                      500);


                  }
                },
                  error => {
                    console.log("error");
                  });
              }
            }
            this.email2 = this.email;
          }
        }
      }
    } else{
      this.ahora++;
    }
  }

  onChangeContra() {
    if (this.re_pass != "" && this.re_pass != null && this.re_pass != undefined) {
      if (this.clave != this.re_pass) {
        this.avisoPassText = 'Las contraseñas no coinciden';
        this.avisoPassTextTipo = 'text-danger';
        this.inputPassvalido = "is-invalid";
      } else {
        this.avisoPassTextTipo = 'text-success';
        this.avisoPassText = '';
        this.inputPassvalido = "is-valid";
      }
    } else {
      this.inputPassvalido = "";
      this.avisoPassText = "";
      this.avisoPassTextTipo = "text-info";
    }
  }

  volveraintentar(){
    this.ahora = 1;
  }

  onRegistroSubmit() {
    const usuario = {
      nombre: this.nombre,
      email: this.email,
      telefono: this.telefono,
      password: this.re_pass,
      ciudad: this.ciudad,
      fecha: this.fecha,
      apellido: this.apellido
    }
    this.authService.registerUser2(usuario).subscribe(data => {
      console.log(data);
      if (data.email != null && data.email != undefined) {
        this.authService.estadoregistro(true);
      } else {
        console.log("no lo creo");
      }
    },
      error => {
        console.log(error);
        this.registroerror = true;
        this.erroremail = error.error.email;
        this.errornombre = error.error.nombre;
        this.errorapellido = error.error.apellido;
        this.errorciudad = error.error.ciudad;
        this.errorfecha = error.error.fecha;
        this.errortelefono = error.error.telefono;
        this.errorpassword = error.error.password;
      });
  }

  darClaseyEstilo() {
    if (this.authService.darclaseRegistro()) {
      this.clase = "container card";
      this.estilo = "width: 20rem; height: 20rem";
    } else {
      this.clase = "container";
      this.estilo = "";
    }
  }

  llevaralogin() {
    this.authService.estadoregistro(true);
  }

}
