import { Component, OnInit } from '@angular/core';
import { QuestionsService } from '../../services/questions.service';
import { AuthService } from '../../services/auth.service';

@Component({
  selector: 'app-questions',
  templateUrl: './questions.component.html',
  styleUrls: ['./questions.component.css']
})
export class QuestionsComponent implements OnInit {


  
  constructor(private authservice: AuthService, private questionsService : QuestionsService) { }
  questions = [];
  actualQuestion = null;
  actualIndex = 0;
  showInic :  boolean ;
  showListaCDTs : boolean;
  showIdea : boolean;

  getQuestions() : void {
      this.questionsService.getQuestions().subscribe(questions => this.questions = questions);

  }

  getActualQuestion(): void {
    this.actualQuestion = this.questions[this.actualIndex];
    this.showInic = false;
  }

  avanzarPositivo() : void {
    if(this.actualIndex < (this.questions.length-1)) 
    {
      this.actualIndex++;
      this.getActualQuestion();
      this.showIdea = true;
    }
    else 
    {
      this.showListaCDTs = true;
    }

  }
  avanzarNegativo() : void {
    if(this.actualIndex < (this.questions.length-1)) 
    {
      this.actualIndex++;
      this.getActualQuestion();
      this.showIdea = false;
    }
    else 
    {
      this.showListaCDTs = true;
    }

  }

  ngOnInit() {
    this.showInic = true;
    this.showListaCDTs = false;
    this.getQuestions();
    this.showIdea = true;
    this.authservice.setInicio(false);
    }

}
