import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs';
import { AuthService } from '../services/auth.service';
const API_URL = "http://157.230.14.37:8080/api/";
const banks = 'bancos';


@Injectable()
export class BankService {
    auto = "token";

    constructor(private http: HttpClient) { }

    getBanks() : Observable<any[]> {
        return this.http.get<any[]>(API_URL + banks);
    }
    getBank(bankName) : Observable<any> {
        return this.http.get<any>(API_URL + banks + '/' + bankName);
    }

    getReviewsBank(slug) : Observable<any> {
        return this.http.get<any[]>(API_URL + banks + '/' + slug + '/calificaciones/'  );
    }

    createReviewBank(slug, review) : Observable<any> {
        const token = localStorage.getItem('id_token');
        const httpOptions = {
            headers: new HttpHeaders({
              'Content-Type': 'application/json',
              'Authorization': 'token ' + token
            })
          };
        return this.http.post<any>(API_URL + banks + '/' + slug + '/calificaciones/', review, httpOptions);
    }
    
}