import { Component, OnInit, HostListener } from '@angular/core';
import { Router } from '@angular/router';
import { interval } from 'rxjs';
import { faCoffee } from '@fortawesome/free-solid-svg-icons';
import { AuthService } from '../../services/auth.service';
import { NgFlashMessageService } from 'ng-flash-messages';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: [
    './home.component.css'
  ]
})
export class HomeComponent implements OnInit {

  correcto = false;
  nombre: String;
  email = "";
  email2 = "";
  telefono: number;
  acepta = false;
  correoValido = false;
  inputvalido = "";
  avisoCorreoText = "";
  avisoCorreoTextTipo = "text-info";
  modaldatatoggle = "";
  modaldatatarget = "";



  tam = 0;
  faCoffee = faCoffee;

  video = {
    width: 0,
    height: 0
  }

  constructor(
    private authService: AuthService,
    private router: Router,
    private flashMessage: NgFlashMessageService) { }




  showNavigationArrows = false;
  showNavigationIndicators = false;

  public innerWidth: any;

  cambiarTamanioVideo = function () {
    this.video.width = (this.innerWidth / 100) * 80;
    this.video.height = (this.video.width / 16) * 9;
  }



  ngOnInit() {
    this.innerWidth = window.innerWidth;
    this.cambiarTamanioVideo();

    const secondsCounter = interval(1000);

    this.authService.setInicio(false);
    secondsCounter.subscribe(n => this.onVerificarCorreo());

  }

  onRegistroSubmit() {
    const usuario = {
      nombre: this.nombre,
      email: this.email,
      telefono: this.telefono,
      acepta: this.acepta
    }
    this.authService.registerUser2(usuario).subscribe(data => {
      console.log("datos: ");
      if (data.status == 201) {
        this.correcto = true;
        console.log("lo creo");
      } else {
        console.log("no lo creo");
      }
    },
      error => {
        console.log("error");
      });
  }

  onVerificarCorreo = function() {
    if(this.email == this.email2){

    }else{
      this.avisoCorreoText = "Validando correo...";
      this.avisoCorreoTextTipo='text-warning';
      var separado = this.email.split("@");
      if (separado.length != 2) {
        console.log('el correo no es válido');
        this.avisoCorreoText='el correo no es válido';
        this.avisoCorreoTextTipo='text-danger';
        this.inputvalido = "is-invalid";
      } else {
        var separado2 = separado[1].split(".");
        if (separado2.length < 2) {
          this.avisoCorreoText='el correo no es válido';
          this.avisoCorreoTextTipo='text-danger';
          this.inputvalido = "is-invalid";
        } else {
          var completo = this.email.replace(/\./g, "%1%");
          completo = completo.replace(/@/g, "%2%");
          console.log(this.email);
          console.log(completo);
  
          this.authService.existeCorreo(completo).subscribe(data => {
            console.log(data);
            if (data.existe == "SI") {
              console.log("si existe");
              this.avisoCorreoText='el correo ya está registrado';
              this.avisoCorreoTextTipo='text-danger';
              this.inputvalido = "is-invalid";
            } else {
              console.log("No existe");
              this.avisoCorreoTextTipo='text-success';
              this.avisoCorreoText='Puedes seguir :)';
              this.inputvalido = "is-valid";
              this.modaldatatoggle = "modal";
              this.modaldatatarget = "#exampleModalLong";
            }
          },
            error => {
              console.log("error");
            });
        }
      }
      this.email2 = this.email;
    }
  }

  @HostListener('window:resize', ['$event'])
  onResize(event) {
    this.innerWidth = window.innerWidth;
    this.cambiarTamanioVideo();
  }

}

