import { Component, OnInit } from '@angular/core';
import { AuthService } from '../../services/auth.service';
import { Router } from '@angular/router';
import { interval } from 'rxjs';
import { NgFlashMessageService } from 'ng-flash-messages';
import { delay } from 'rxjs/operators';

@Component({
  selector: 'app-logueado',
  templateUrl: './logueado.component.html',
  styleUrls: ['./logueado.component.css']
})
export class LogueadoComponent implements OnInit {
  cont: number;
  segundociclo = false;


  constructor(
    private authService: AuthService,
    private router: Router,
    private flashMessage: NgFlashMessageService) { }

  ngOnInit() {
    this.cont = 1;
    const secondsCounter = interval(2000);
    secondsCounter.subscribe(n => {
      this.segundociclo = this.authService.sacarsegundociclo();
      this.cont = this.cont + 1;
      if (this.cont == 11 && !this.segundociclo && this.authService.loggedIn() && this.authService.darInicio()) {
        this.authService.guardarsegundociclo(true);
        this.router.navigate(['/questions']);
      }
    });
  }

}