import { Component, OnInit } from '@angular/core';
import { BankService } from 'src/app/services/bank.service';
import { DataService } from 'src/app/services/data.service';
import { AuthService } from '../../services/auth.service';
import { interval } from 'rxjs';

@Component({
  selector: 'app-detail-bank',
  templateUrl: './detail-bank.component.html',
  styleUrls: ['./detail-bank.component.css']
})
export class DetailBankComponent implements OnInit {

  constructor(private bankService: BankService, private dataService: DataService, private authservice: AuthService) {
    this.posActual = 0;
  }

  bancoSeleccionado: any;
  bankName: string;
  bankName2 = "aklsdklad12";
  cargado = false;

  comentario: string;
  puntaje: number;


  resenias = [];

  calificacion = [];

  posActual: number;

  showCreate: boolean;



  getBank() {
    this.bankName = this.dataService.obtenerSlug();
    if (this.bankName != this.bankName2) {
      if (this.bankName !== undefined) {
        this.bankService.getBank(this.bankName)
          .subscribe(bancoSeleccionado => {
            this.bancoSeleccionado = bancoSeleccionado;
            this.bancoSeleccionado.calificacion_promedio = Math.round(this.bancoSeleccionado.calificacion_promedio);
            this.calificacion.length = this.bancoSeleccionado.calificacion_promedio;
            this.bankService.getReviewsBank(this.bankName)
              .subscribe(calificaciones => {
                this.resenias = calificaciones.results;
                this.cargado = true;
                this.bankName2 = this.bankName;
              });

          });
      }
    }

  }

  createResena() {
    var resenia = {
      comentario: this.comentario,
      puntaje: this.puntaje
    }
    console.log(this.bankName);
    this.bankService.createReviewBank(this.bankName, resenia)
      .subscribe(respons => {
        this.showCreate = false;
        this.comentario = '';
      },
        error => {
          console.log(error.error)
        });
  }

  getResenias() {
    this.bankService.getReviewsBank(this.bankName)
      .subscribe(calificaciones => {
        this.resenias = calificaciones.results;
        this.cargado = true;
        this.bankName2 = this.bankName;
      });
  }

  showHideCreate() {
    this.showCreate = !this.showCreate;
  }


  ngOnInit() {
    this.showCreate = false;
    const secondsCounter = interval(100);
    secondsCounter.subscribe(n => this.getBank());
    this.authservice.setInicio(false);

  }

}
